import React, { useState, useEffect } from "react";
import TextField from "../common/form/textField";
import ShoppingCardButton from "../shoppingCart/shoppingCartButton";
import CheckBoxField from "../common/form/checkBoxField";
import { validator } from "../../utils/validator";
import classes from "../../modules/textField.module.css";
import { signUp as signUpRedux } from "../../store/user";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import PropTypes from "prop-types";

const AccountRegisterForm = ({ closeModal }) => {
    const [data, setData] = useState({
        name: "",
        telephone: "",
        secondName: "",
        email: "",
        password: "",
        secondPassword: "",
        licence: false
    });
    const [errors, setErrors] = useState({});
    const [hoverButton, setHoverButton] = useState(false);
    const dispatch = useDispatch();
    const history = useHistory();
    const toggleHoverButton = () => {
        setHoverButton(!hoverButton);
    };
    const handleChange = (target) => {
        setData((prevState) => ({
            ...prevState,
            [target.name]: target.value
        }));
    };
    useEffect(() => {
        validate();
    }, [data]);
    const validate = () => {
        const errors = validator(data, validatorConfig);
        setErrors(errors);
        return Object.keys(errors).length === 0;
    };
    const isValid = Object.keys(errors).length === 0;
    const handleSubmit = (e) => {
        e.preventDefault();
        const isValid = validate();
        if (!isValid) return;
        const transformData = {
            ...data,
            bought: 0,
            shoppingCart: [],
            viewed: [],
            orders: [],
            isAdmin: false
        };
        delete transformData.secondPassword;
        dispatch(signUpRedux(transformData));
        setTimeout(() => {
            closeModal();
            history.replace("/persaccount");
        }, 1000);
    };
    const validatorConfig = {
        email: {
            isEmail: {
                message: "Email  "
            }
        },
        name: {
            minSymbols: {
                message: "     3 ",
                value: 3
            }
        },
        password: {
            isCapitalSymbol: {
                message: "       "
            },
            isContainDigital: {
                message: "      "
            },
            minSymbols: {
                message: "     8 ",
                value: 8
            }
        },
        telephone: {
            minSymbols: {
                message: "     11 ",
                value: 11
            }
        },
        secondPassword: {
            isDifference: {
                message: "  ",
                passwordValue: data.password
            }
        },
        licence: {
            isRequired: {
                message:
                    "         "
            }
        }
    };
    return (
        <>
            <form onSubmit={handleSubmit} className={classes.formWrapper}>
                <TextField
                    placeholder=" "
                    label=" *"
                    name="name"
                    value={data.name}
                    onChange={handleChange}
                    error={errors.name}
                />
                <TextField
                    placeholder=" "
                    label=""
                    name="secondName"
                    value={data.secondName}
                    onChange={handleChange}
                    error={errors.secondName}
                />
                <TextField
                    placeholder=" Email"
                    label="Email *"
                    name="email"
                    value={data.email}
                    onChange={handleChange}
                    error={errors.email}
                />
                <TextField
                    placeholder=""
                    label="  *"
                    name="telephone"
                    value={data.telephone}
                    onChange={handleChange}
                    error={errors.telephone}
                />
                <TextField
                    placeholder=" "
                    label=" *"
                    name="password"
                    value={data.password}
                    type="password"
                    onChange={handleChange}
                    error={errors.password}
                />
                <TextField
                    placeholder=" "
                    label="  *"
                    name="secondPassword"
                    type="password"
                    value={data.secondPassword}
                    onChange={handleChange}
                    error={errors.secondPassword}
                />
                <CheckBoxField
                    value={data.licence}
                    onChange={handleChange}
                    name="licence"
                    error={errors.licence}
                >
                    <span className={classes.checkBox}>
                            ,  
                          
                    </span>
                </CheckBoxField>
                <div style={{ marginTop: "30px" }}>
                    <ShoppingCardButton
                        onClick={handleSubmit}
                        title=""
                        orange={true}
                        onHoverButton={toggleHoverButton}
                        hoverButton={hoverButton}
                        icon={false}
                        buttonWidth={"250px"}
                        modal={false}
                        disabled={!isValid}
                    />
                </div>
            </form>
        </>
    );
};

AccountRegisterForm.propTypes = {
    closeModal: PropTypes.func
};

export default AccountRegisterForm;
